/*----------------------------------------------------------------------------------------*/

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px rgb(29, 93, 182);
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(29, 93, 182);
  border-radius: 10px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Poppins', sans-serif;
}

p{
  font-family: 'Quicksand', sans-serif;
}

.meta-nav {
    min-height: 40px;
    background: white;
    color: #01579b;
    padding: 0px;
    margin: 0px;
    width: 100%;
}


.meta-ieee-logo {
      margin: -2px 0px 0px 0px;
    float:right;
}

@media (max-width: 768) {
.meta-nav {
