#ieee-meta-a {
    font-family: inherit;
    font-size: .9em;
    line-height: 125%;
    border: 0;
    margin: 0;
    color: #01579b;
    text-align: left;
    padding: 11px 36px;
}

#ieee-meta-a a:link, #ieee-meta-a a:visited, #ieee-meta-a a:active {
    color: #01579b;
    text-decoration: none;
}

#ieee-meta-a a:hover {
    color: #01579b;
    text-decoration: underline;

}

.toolbar_a{
    position: fixed;
    width: 100%;
    left: 0;
    top: 40px;
    background: white;
    height: 30px;
    z-index:5;
    padding: 2rem;
    padding-bottom: 1.7rem;
    padding: 2.5rem;
    padding-bottom: 2.5rem;
    outline: 0;
}
.toolbar_navigation{
    background-color: white;
    display: flex;
    height: 100%;
    align-items: center;
    padding: 0 1rem;
    box-shadow: none;
    outline: 0;
}

.drawer_toggle{
    align-items: center;
    margin-top: 0.5rem;
    outline: 0;
}
.toolbar_logo a{
    color: #01579b;
    text-decoration: none;
    font-size: 1rem;
    margin-left: 0.5rem;
    outline: 0;

}
.hi{
    flex:1 1;
}
.toolbar_navigation_items ul{
    color: #01579b;
    text-decoration: none;
    list-style: none;
    font-size: 1.25rem;
    display: flex;
    position: relative;
    float: right;
    outline: 0;


}
.toolbar_navigation_items li{
    padding: 0 0.5rem;
    outline: 0;
}
.toolbar_navigation_items a{
    line-height:50px;
    color: #01579b;
    text-decoration: none;
    border-radius: 0px;
    outline: 0;
    padding-left: 30px;
    padding-right: 30px;
}

@media(max-width: 900px){
    .toolbar_navigation_items{
        display: none;
        position: fixed;
    }

    .toolbar_a{
        position: fixed;
        width: 100%;
        left: 0;
        top: 40px;
        background: rgb(255, 255, 255);
        height: 56px;
        z-index:5;
        padding: 2px;
    }
    .active{
      background-color: white;
      text-decoration: underline;
      color: #01579b;
    }
}

.toggle_button{
    border: none;
    background-color: white;
    align-items: center;
    background: transparent;
    cursor: pointer;

}
.toggle_button:focus{
    outline: none;
}
@media(min-width:900px){
    .toggle_button{
        display: none;
    }
}

